import { Injectable } from '@angular/core';
import { HttpService } from '../../core/services/http.service';
import { AuthService } from '../../core/services/auth.service';

@Injectable({
    providedIn: 'root'
})
export class UserDashboardService {

    constructor(public http: HttpService, public auth: AuthService) { }

    getProfileDetails() {
        return this.http.get('api/v1/user/profile', {}, { auth: true });
    }

    updateProfileDetails(data) {
        let payload = {};
        const a = ['personalDetails', 'appearance', 'arts', 'sports', 'features', 'combatTraining', 'otherSkills', 'experience'];
        for (const key in data) {
            if (a.includes(key)) {
                if (key === 'experience') {
                    payload['experience'] = data[key];
                } else {
                    payload = { ...payload, ...data[key] };
                }
            }
        }
        return this.http.put('api/v1/user/profile', payload, { auth: true });
    }

    uploadUserImage(image) {
        const payload = new FormData();
        payload.append('file', image, `img-${this.auth.getUserId()}`);
        return this.http.post('api/v1/user/image', payload, { auth: true, contentType: 'multipart/form-data' });
    }

    portfolio(payload) {
        return this.http.post('api/v1/user/portfolio', payload, { auth: true, contentType: 'multipart/form-data' });
    }

    getPortfolio(id) {
        let url = 'api/v1/user/portfolio'
        const header = { auth: true };
        if (id) {
            url += '/' + id;
            header.auth = false;
        }
        return this.http.get(url, {}, header);
    }

    getDropdownValues() {
        return this.http.get('api/v1/user/dropdown', {}, { auth: true });
    }

    updatePassword(payload) {
        return this.http.put('api/v1/user/changepassword', payload, { auth: true });
    }

    getPortfolioCounts() {
        if (this.auth.isAuthenticated()) {
            return this.http.get('api/v1/portfolio/counts', {}, { auth: true });
        } else {
            return Promise.resolve({});
        }
    }

    deleteMedia(payload) {
        return this.http.post('api/v1/user/portfolio/delete', payload, { auth: true });
    }
}
