import { Component, OnInit } from '@angular/core';
import { UserDashboardService } from '../user-dashboard.service';
import { DialogService } from '../../../core/services/dialog.service';
// , FeaturesDetailsLookup, CombatTrainingDetailsLookup, OtherSkillsDetailsLookup
import { PersonalDetailsLookup, AppearanceDetailsLookup, ArtsDetailsLookup, SportsDetailsLookup, ProfileDetails } from './profile';
